import React from 'react';
import './App.css';
import Appheader from './Appheader.js'
import SideNavigations from './SideNavigations.js'
import AppContent from './AppContent.js'



class App extends React.Component{
  constructor(props){
    super(props);

    this.state = {
      title : "Keep",
      isDrawerOpened : false,
      NotesAlignment: 'grid',
      searchKey: '',
    }
    this.giveTitle = this.giveTitle.bind(this);
    this.initSideDrawer = this.initSideDrawer.bind(this);
  }

  giveTitle(newtitle){
      this.setState({
        title : newtitle==="notes" ? "Keep" : newtitle,
      });
  }
  
  initSideDrawer(){
      this.setState({
        isDrawerOpened : !this.state.isDrawerOpened,
      });
  } 
  
  toggleNotesView = (type) => {
      this.setState({
          NotesAlignment: type
      })
  }

  onSearch = e => {
    this.setState({
      searchKey: e.target.value,
    })
  }

  render(){

    return (
      <div className="App">
            <Appheader title={this.state.title} initSideDrawer={this.initSideDrawer} toggleNotesView={this.toggleNotesView} onSearch={this.onSearch}/>
            <div className="content-body">
              {
                this.state.searchKey===''
                ?
                <SideNavigations giveTitle={this.giveTitle} drawer={this.state.isDrawerOpened} />
                :
                <div></div>
              }
              <AppContent NoteView={this.state.NotesAlignment} searchKey={this.state.searchKey}/>
            </div>
      </div>
    );

  }
}

export default App;
