import React from 'react';
import './AppContent.css'
import ShortView from './ShortView.js'
import FullView from './FullView.js'
import empty from "../assets/Empty-notes-icon.png";
import TextareaAutosize from 'react-textarea-autosize';

const listView = {
    width: '100%',
    border: '1px solid rgb(200,200,200)',
    borderRadius: '5px',
    margin: '5px',
    padding: '10px'
}

const gridView = {
    width: '200px',
    border: '1px solid rgb(200,200,200)',
    borderRadius: '5px',
    margin: '5px',
    padding: '10px',
    overflow: 'auto'
}

const CreateNoteDiv = ({title, desc, NoteView}) => {
    return (
        <div style={NoteView==='list' ? listView : gridView}>
            <div style={{fontWeight:'bold'}}>{`${title}`}</div>
            <TextareaAutosize 
                style={{
                    width:'100%',
                    outline: 'none',
                    border: 'none',
                    resize: 'none',
                    fontSize: '18px',
                    opacity: '0.8',
                    fontFamily: 'Cabin sans-serif'
                }}
                value={`${desc}`}
                ></TextareaAutosize>
        </div>
    )
}
class AppContent extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            expand : false,
            notes : [],
        }

        this.openFullTitleBox = this.openFullTitleBox.bind(this);
    }

    openFullTitleBox(){
        this.setState(prevState => ({
            expand : !prevState.expand,
        }))
    }

    createNote = (Notetitle, Notedescription) => {
        this.state.notes.push({title:Notetitle, description: Notedescription});
    }

    render(){
        return (
            <>
            {
                this.props.searchKey!==''
                ?
                <div className="inner-container" style={this.props.NoteView==='list' ? {width:'50%'} : {width:'80%'}}>
                    {
                        this.state.notes.filter(note => 
                        (note.title.includes(this.props.searchKey) ||  note.description.includes(this.props.searchKey)))
                        .map((note,index)=>
                            <CreateNoteDiv title ={note.title} desc={note.description} NoteView={this.props.NoteView} key={index}/>
                        )
                    }
                </div>
                :
                <div className="right-side-content-div">
                    <div className="title-bar">
                        {
                        this.state.expand ? 
                                <FullView 
                                    expand={this.openFullTitleBox}
                                    createNotes={this.createNote}
                                    /> 
                                    : 
                                <ShortView 
                                    expand={this.openFullTitleBox}
                                    />
                        }
                    </div>
                    <div className="inner-container" style={this.props.NoteView==='list' ? {width:'50%'} : {width:'80%'}}>
                        {
                            this.state.notes.map((note,index) => <CreateNoteDiv title ={note.title} desc={note.description} NoteView={this.props.NoteView} key={index}/>)
                        }
                    </div>
                    <div style={this.state.notes.length ? 
                        {display:'none'} : 
                        {
                            display:'flex',
                            flexDirection:'column', 
                            alignItems: 'center',
                            marginTop: '100px'
                            }}>
                        <img src={empty} alt="Empty Section" style={{width:'80px', height:'120px'}}/><br/>
                        <span style={{opacity:'0.6',fontSize:'22px'}}>Notes you add appear here</span>
                    </div>
                </div>
            }
            </>
        )
    }
}

export default AppContent;