import React from 'react';
import logo from '../assets/title-icon.png';
import './Appheader.css';
import MouseOverPopover from './Popover.js'

class Appheader extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            name: "",
            list: true,
            grid: false,
         }

        this.searchBarHover = this.searchBarHover.bind(this);
    }

    searchBarHover(){
        let style = this.state.name==="" ? "search-div-effects" : "";
        this.setState({
            name: style,
        });
    }

    toggleViewIcons(){
        this.setState({
            list: !this.state.list,
            grid: !this.state.grid,
        });
    }

    render(){
        return (
            <div className="navbar">
                
                {/* <!-- left side nav bar --> */}
                <div className="left-side-div">
                    <div className="menu-bar-icon" onClick={this.props.initSideDrawer}>
                        <span className="material-icons-outlined" title="main menu">menu</span>
                    </div>
                    <div className="logo-div">
                        <img className="logo" src={logo} alt="logo"/>
                        <span className="logo-text">{this.props.title}</span>
                    </div>
                </div>
    
                {/* <!-- middle nav bar --> */}
                <div className="middle-div">
                    <div className={`search-div ${this.state.name}`}>
                        <div className=" search-icon">
                            <span className="material-icons-outlined" title="Search">search</span>
                        </div>
                        <input className="search-bar" type="text" placeholder="Search" onFocus={this.searchBarHover}
                            onBlur={this.searchBarHover} onChange={this.props.onSearch}/>
                    </div>
                </div>
                
                {/* right side nav bar */}
                <div className="right-side-div">
                    <div className="settings" title="Settings">
                        <span className="material-icons-outlined" >settings</span>
                    </div>
                    <div className="grid-view" title="Grid View" 
                        onClick={() => {
                            this.props.toggleNotesView('grid')
                            this.toggleViewIcons()
                        }}
                        style={this.state.grid ? {display:'block'}:{display:'none'}}
                        >
                        <span className="material-icons-outlined">grid_on</span>
                    </div>
                    <div className="list-view" title="List View" 
                        onClick={() => {
                            this.props.toggleNotesView('list')
                            this.toggleViewIcons()
                        }}
                        style={this.state.list ? {display:'block'}:{display:'none'}}
                        >
                        <span className="material-icons-outlined">view_stream</span>
                    </div>
                    <div className="refresh" title="Refresh">
                        <span className="material-icons-outlined">refresh</span>
                    </div>
                    <div className="mini-search" title="Search">
                        <span className="material-icons-outlined">search</span>
                    </div>
                </div>
                
                {/* right most div */}
                <div className="right-most-div">
                    <div className="login-div">
                        <MouseOverPopover />
                    </div>
                    <div className="more-apps" title="More Apps">
                        <span className="material-icons-outlined" >apps</span>
                    </div>
                </div>
            </div>
        )
    }
}

export default Appheader;