import React from 'react';
import { Popover, Button } from '@material-ui/core';
import LoginPage from './LoginPage.js'


export default function MouseOverPopover() {

    //dialog
    const [opened, setOpened] = React.useState(false);

    const handleClickOpen = () => {
        setOpened(true);
    };

    const handleClose = (value) => {
        setOpened(false);
    };

    //popover
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClosePop = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;

    return (
        <div>
            <span className="material-icons-outlined" onClick={handleClick}>face</span>
            <Popover
                id={id}
                open={open}
                anchorEl={anchorEl}
                onClose={handleClosePop}
                anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'center',
                }}
                transformOrigin={{
                vertical: 'top',
                horizontal: 'center',
                }}
            >
                <Button variant="contained" onClick={handleClickOpen} style={{background:'rgb(245, 180, 15)', color:'rgb(255,255,255)'}}>
                    Login to save notes
                </Button>
                <LoginPage open={opened} onClose={handleClose} />
            </Popover>
        </div>
    );
}
