import React, { useEffect, useRef, useState } from 'react';
import TextAreaAutoSize from 'react-textarea-autosize';

import './FullView.css'

const FullView = (props) => {
    const [pinned, setPinned] = useState(false)
    const titleRef = useRef()
    const noteRef = useRef()

    function sendNotesDataToParent(Notetitle, Notedescription){
        props.createNotes(Notetitle,Notedescription);
    }

    const closeContainer = () => {
        if(titleRef.current.value!=='' || noteRef.current.value!=='')
            sendNotesDataToParent(titleRef.current.value, noteRef.current.value);
        props.expand();
    }

    useEffect(() => {
        const caller = e => {
                let node = e.target;
                const title_container = document.querySelector('.complete-note-container');
                while(node!==document && node!==title_container)
                    node = node.parentNode;

                if(node===document){
                    closeContainer();
                }
        };

        window.addEventListener('click', caller);

        return () => {
            window.removeEventListener('click',caller)
        }
    })
    return (
        <div className="complete-note-container">
            <div className="title-section">
                <TextAreaAutoSize placeholder="Title" className="text-field large" ref={titleRef}/>
                <div className="hover-effect" onClick={()=>setPinned(!pinned)}>
                    <span className={`material-icons${!pinned ? '-outlined' : ''}`} title="pin">push_pin</span>
                </div>
            </div>
            <div>
                <TextAreaAutoSize placeholder="Take a note..." className="text-field small" ref={noteRef} autoFocus/>
            </div>
            <div className="bottom-options">
                <div className="left-side-options">
                    <div className="hover-effect">
                        <span className="material-icons-outlined small-icon" title="Remind me">add_alert</span>
                    </div>
                    <div className="hover-effect">
                        <span className="material-icons-outlined small-icon" title="Collaborator">person_add_alt_1</span>
                    </div>
                    <div className="hover-effect">
                        <span className="material-icons-outlined small-icon" title="Change colors">color_lens</span>
                    </div>
                    <div className="hover-effect">
                        <span className="material-icons-outlined small-icon" title="Add image">insert_photo</span>
                    </div>
                    <div className="hover-effect">
                        <span className="material-icons-outlined small-icon" title="Archive">archive</span>
                    </div>
                    <div className="hover-effect">
                        <span className="material-icons-outlined small-icon" title="More">more_vert</span>
                    </div>
                    <div className="hover-effect">
                        <span className="material-icons-outlined small-icon" title="Undo">undo</span>
                    </div>
                    <div className="hover-effect">
                        <span className="material-icons-outlined small-icon" title="Redo">redo</span>
                    </div>
                </div>
                <div className="right-side-options">
                    <button className="close-btn" onClick={closeContainer}>Close</button>
                </div>
            </div>
        </div>
    )
}

export default FullView;