import React from 'react';
import './SideNavigations.css'

class SideNavigations extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            onHover: "no-hovers",
            topLevelStyle:"",
            show:"showLess",
            activeTab:"notes",
        }
        this.toggleSideNavigations = this.toggleSideNavigations.bind(this);
    }

    toggleSideNavigations(){
        let first = this.state.onHover==="no-hovers" ? "hovers" : "no-hovers";
        let second = this.state.topLevelStyle==="" ? "single-navigation" : "";
        let third = this.state.show==="showLess" ? "showFull" : "showLess";
        this.setState({
            onHover: first,
            topLevelStyle: second,
            show: third,
        });
    }
     
    applyActive = (value) => {
        this.setState({
            activeTab: value,
        })
        this.props.giveTitle(value);
    }

    componentDidUpdate(prevProps){
        if(prevProps.drawer!==this.props.drawer)
            this.toggleSideNavigations();
    }
    render(){

        return (
            <div className={`left-side-navigation-div ${this.state.topLevelStyle}`} 
                onMouseOver={!this.props.drawer ? this.toggleSideNavigations : ()=>{}} onMouseOut={!this.props.drawer ? this.toggleSideNavigations : ()=>{}}>
                <div className={`notes ${this.state.onHover}`} id={this.state.activeTab==="notes" ? "active" : ""} onClick={()=>this.applyActive("notes")}>
                    <div className="material-icons-outlined">emoji_objects</div>
                    <span className={`notes-span ${this.state.show}`}>Notes</span>
                </div>
                <div className={`reminders ${this.state.onHover}`} id={this.state.activeTab==="reminders" ? "active" : ""} onClick={()=>this.applyActive("reminders")}>
                    <div className="material-icons-outlined">notifications_none</div>
                    <span className={`reminders-span ${this.state.show}`}>Reminders</span>
                </div>
                <div className={`edit-label ${this.state.onHover}`} id={this.state.activeTab==="edit-label" ? "active" : ""}>
                    <div className="material-icons-outlined">create</div>
                    <span className={`edit-label-span ${this.state.show}`}>Edit Labels</span>
                </div>
                <div className={`archive ${this.state.onHover}`} id={this.state.activeTab==="archive" ? "active" : ""} onClick={()=>this.applyActive("archive")}>
                    <div className="material-icons-outlined">archive</div>
                    <span className={`archive-span ${this.state.show}`}>Archive</span>
                </div>
                <div className={`bin ${this.state.onHover}`} id={this.state.activeTab==="bin" ? "active" : ""} onClick={()=>this.applyActive("bin")}>
                    <div className="material-icons-outlined">delete</div>
                    <span className={`bin-span ${this.state.show}`}>Bin</span>
                </div>
            </div>
        )

    }
}

export default SideNavigations;