import React from 'react';
import './ShortView.css'

class ShortView extends React.Component{
    render(){
        return (
            <div className="title-container">
                <div className="note-text-section">
                    <input type="text" placeholder="Take a note..." onFocus={this.props.expand} onBlur={this.props.expand}/>
                </div>
                <div className="side-options">
                    <div className="new-note">
                        <span className="material-icons-outlined">assignment_turned_in</span>
                    </div>
                    <div className="new-note-with-drawing">
                        <span className="material-icons-outlined">brush</span>
                    </div>
                    <div className="new-note-with-image">
                        <span className="material-icons-outlined"> insert_photo</span>
                    </div>
                </div>
            </div>
        )
    }
}

export default ShortView;